@font-face {
  font-family: "dots";
  src: url("../fonts/dots/dots.eot");
  src: url("../fonts/dots/dots.eot?#iefix") format("embedded-opentype"), url("../fonts/dots/dots.woff") format("woff"), url("../fonts/dots/dots.ttf") format("truetype"), url("../fonts/dots/dots.svg#dotsfontregular") format("svg");
}
.font-dots {
  font-family: "dots";
  color: #666;
  line-height: 1rem;
  font-size: 8px;
}

.upvoty-wp-integration-settings .inline {
  display: inline;
}
.upvoty-wp-integration-settings .row {
  display: table;
  content: "";
  width: 100%;
}
.upvoty-wp-integration-settings .row, .upvoty-wp-integration-settings .row:after {
  clear: both;
}
.upvoty-wp-integration-settings .col {
  float: left;
  min-height: 1px;
  width: 100%;
}
@media (min-width: 768px) {
  .upvoty-wp-integration-settings .col-md-6 {
    width: 50%;
  }
}
.upvoty-wp-integration-settings .my-1 {
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
}
.upvoty-wp-integration-settings .color-green {
  color: #19a974;
}
.upvoty-wp-integration-settings .color-light-gray {
  color: #999;
}
.upvoty-wp-integration-settings fieldset {
  margin: 15px 0 25px;
}
.upvoty-wp-integration-settings fieldset > *:first-child {
  margin-top: 0;
}
.upvoty-wp-integration-settings label {
  display: block;
  margin-bottom: 0.75rem;
  font-size: 1.125em;
  font-weight: 500;
  cursor: default;
}
.upvoty-wp-integration-settings ul {
  list-style: disc;
  padding-left: 18px;
}
.upvoty-wp-integration-settings ul li {
  line-height: 1.5;
}
.upvoty-wp-integration-settings .upvoty-wp-logo {
  display: inline-block;
  vertical-align: middle;
  margin-right: 4px;
}
.upvoty-wp-integration-settings .upvoty-wp-logo img {
  max-width: 60px;
  border-radius: 50%;
}
.upvoty-wp-integration-settings .getting-started {
  margin-top: -0.5rem;
}
.upvoty-wp-integration-settings .getting-started ol {
  margin-left: 1.25rem;
}
.upvoty-wp-integration-settings .attributes-table {
  margin-top: 1.25rem;
  text-align: left;
}
.upvoty-wp-integration-settings .attributes-table th {
  padding: 0.25rem 0 1rem 0;
}
.upvoty-wp-integration-settings .attributes-table td, .upvoty-wp-integration-settings .attributes-table td * {
  vertical-align: top;
}
.upvoty-wp-integration-settings .attributes-table td {
  padding-bottom: 1rem;
}
.upvoty-wp-integration-settings .attributes-table td p:first-child {
  margin-top: 0;
}
.upvoty-wp-integration-settings .attributes-table td p:last-child {
  margin-bottom: 0;
}
.upvoty-wp-integration-settings .attributes-table td:first-child {
  width: 220px;
}
.upvoty-wp-integration-settings .section-integrations td:nth-child(2) {
  width: 120px;
}

.upvoty-wp-integration-settings-tab-settings {
  margin: 20px 0;
}
.upvoty-wp-integration-settings-tab-settings input[type=text],
.upvoty-wp-integration-settings-tab-settings input[type=password] {
  width: 220px;
}
.upvoty-wp-integration-settings-tab-settings .field-description {
  margin: 10px 5px;
}
.upvoty-wp-integration-settings-tab-settings input.field-jwt-private-key {
  width: 300px;
}
.upvoty-wp-integration-settings-tab-settings .submit {
  margin: 0 0 1.5rem;
  padding: 0;
}